
















































































































































































































































































	export default {
		data(){
			return{
				title:'会员购买',
				isAlert:false, //弹窗
				isNav:true, //控制底部导航栏显示
				isSureAlert:false, //控制确认升级页面显示
				grade:'',
				isSuccess:false, //购买成功页面显示
				way:'weichat',
				money:'',
				father:{
					id:1,
					username:'',
					avatar_url:''
				},
				infoIndex:3,
			  scale:30,
				goodlist:[],
				goodsId:'',
				role:'',
				scale2:''
			}
		},
		methods:{
			lookInfo(index){
				if(index === this.infoIndex){
					this.infoIndex = '';
				}
				else{
					this.infoIndex = index;
				}
			},
			toBuy(data){
				this.isAlert = true;
				this.grade = data;
				if(data === this.goodlist[0].goods_name){
					this.money = this.goodlist[0].goods_price;
					this.scale = 60;
					this.scale2 = '30%';
					this.goodsId = this.goodlist[0].goods_id;
				}
				else if(data === this.goodlist[1].goods_name){
					this.money = this.goodlist[1].goods_price;
					this.scale = 80;
					this.scale2 = '20%-50%';
					this.goodsId = this.goodlist[1].goods_id;
					
				}
				else {
					this.money = this.goodlist[2].goods_price;
					this.scale = 95;
					this.scale2 = '15%-65%';
					this.goodsId = this.goodlist[2].goods_id;
				}
			},
			alertCancel(){
				this.isAlert = false;
			},
			alertSure(){
				this.isAlert = false;
				this.isNav = false;
				this.isSureAlert = true;
			},
			cancelBuy(){
				this.isSureAlert = false;
			},
			payAct(){
				this.isSureAlert = false;
				this.isNav = true;
					uni.request({
						url: this.$apis.weixinpay, 
						method: 'GET',
						data: {
							type:0,
							goods_id:this.goodsId
						},
						header: {
							'Authorization': uni.getStorageSync('jwt') //获取token值
						},
						success: function(res) {
							let def = res.data.data;
							let timestamp = JSON.stringify(def.timestamp);
							uni.requestPayment({
								provider: 'wxpay',
								timeStamp: timestamp,
								nonceStr: def.noncestr,
								package: def.package,
								signType: 'MD5',
								paySign: def.sign,
								orderInfo:def,
								success: function (res) {
									this.$getInfo();
									this.isSuccess = true;
								}.bind(this),
								fail: function (err) {
									uni.showModal({
										content: '支付失败',
										showCancel: false
									})
								}
							});
						}.bind(this)
				})
			},
			gotoLookActi(){
        this.$navigateTo({
          url: '/pages/index/myActivate'
        })
			},
			gotoIndex(){
				this.isSuccess = false;
				uni.switchTab({
					url: '/pages/index/index'
				})
			},
			sleWay(data){
				this.way = data;
			}
		},
		computed: {
			
		},
		onLoad() {
			//初始化商品列表
			this.role = this.$store.state.userInfo.role;
			this.$http({
			  url: this.$apis.goodlist,
			  method: 'GET',
				data:{
					type:2,
				},
			  cb: (err, data) => {
			    if (!err && data.success) {
						this.goodlist = data.data;
			    } 
					else {
			     
			    }
			  }
			})
			// 获取上级信息
			this.$http({
			  url: this.$apis.kf,
			  method: 'GET',
			  cb: (err, data) => {
			    if (!err && data.success) {
						this.father = data.data;
			    } 
					else {
			     
			    }
			  }
			})
		}
	}
